ipe-cell>\n    <van-swipe-cell v-if=\"user.personalId\">\n      <van-cell :border=\"false\" icon=\"edit\" title=\"Personal ID\" :value=\"user.personalId\"/>\n      <template #right>\n        <van-button square type=\"warning\" text=\"Clear\" @click=\"user.reset\"/>\n      </template>\n    </van-swipe-cell>\n  </van-cell-group>\n</template>\n\n<style scoped>\n.van-image {\n  filter: invert(100%);\n}\n</style>\n"],"sourceRoot":""}]);
